html{
  padding: 0px 20px 20px 20px;
}

.height-10{
  height: 10px;
}

body {
  overflow: auto;
}

#dropzone{
  padding: 0.7rem;
}

.table-text-size{
  font-size: 12px;
}

.tableContainer, .selectedTableContainer{
  display: flex;
  flex-direction: column; /* stacks tables vertically */
  align-items: left; /* center tables horizontally */
  width: 100vw; /* sets the width to the viewport width */
  max-height: 100vh; /* sets the max height to the viewport height */
}

.margin-left-30{
  margin-left: 30px;
}

.flex_horizontal{
  display: flex;
}

.data-table {
  position: relative; /* needed to position the rectangle absolute to this parent */
  padding-top: 0px; /* adjust to the size of your rectangle */
  width: 90%;
}
.summary{
  font-size: 1rem;
  line-height: 1.7rem;
  color: black;
}

.summary-col{
  text-align: left;
  font-weight:normal;
}

.textTag{
  display: inline-block;
    color: #444;
    font-size: 10px;
    background-color: #eee; /* background color */
    border: 1px solid #ddd; /* border around the text */
    padding: 0px 5px; /* space around the text */
    margin: 2px;
    text-align: center;
    text-decoration: none;
}

.textbox{
  width: 85%;
  height: 90%;
  word-break:break-all;
}

.textbox span{
  font-weight: bold;
}

.table-frame, .white-rect{
  width: 100%; /* adjust as needed */
  height: 30vh; /* adjust as needed */
}

.white-rect {
  position: absolute;
  top: 0;
  background-color: white;
  border: 5px solid black; /* if you want a border */
  opacity: 95%;
}

.summary-col{
  height:150px;
  background-color: lightgrey;
}

.table-frame{
  overflow: auto; /* add scrollbars as necessary */
  margin-top: 2rem; /* space between tables */
  /* additional table styles here */
  border:5px solid black;
}

.data-table:hover .white-rect{
  display: none
}

.data-table {
  table-layout: fixed; /* Makes equal-width cells */
}

.data-table th, 
.data-table td {
  width: 150px; /* Allows cell width to be set by table-layout */
  overflow: hidden; /* Ensures content does not spill over */
  text-overflow: ellipsis; /* Nice handling of overflown text */
  white-space: nowrap; /* Keeps cell content on single line */
}

th, td {
  padding: 3px;
}

tr:nth-child(even) {
  background-color: #D6EEEE;
}

.black_border{
  width: 400px;
  border:5px solid black;
}

.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0,0,0,0.4);
}

.modal-content {
  background-color: #fefefe;
  /* margin: 1% auto; */
  padding: 20px;
  border: 1px solid #888;
  width: 100vw;
  overflow: auto;
}

.data-table-wrapper {
  position: relative; /* This creates a positioning context for absolutely positioned children */
}

.modal-btn {
  position: absolute; /* This positions the button relative to the nearest positioned ancestor */
  top: 0.8rem; /* Adjust as necessary */
  left: 0rem; /* Adjust as necessary */
}


.close-button {
  color: #aaa;
  float: left;
  font-size: 35px;
  font-weight: bold;
}

.close-button:hover,
.close-button:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}



/* charts */
.sm_chart_label{
  font-size: 8px;
}